import React from "react";
import styles from "./Header.module.css";
import logo from "./logo text.svg";

const Header = ({changeLayout}) => {
    return <header className={styles.header}>
        <h1 className={[styles.title, "unresponsive-text"].join(" ")}>המגפון החזותי</h1>
        <img className={styles.pinkFront} src={logo} alt={"Pink Front"} />
    </header>
}


export default Header;