import React from "react";
import styles from "./Sidebar.module.css";
import DisplayPicker from "./DisplayPicker";


const Sidebar = ({currentDisplayStyle, setDisplayStyle}) => {
    return <div className={styles.sideBar}>
        <ShoutMenu />
        <DisplayPicker currentDisplayStyle={currentDisplayStyle} setDisplayStyle={setDisplayStyle} />
    </div>
}

const ShoutMenu = () => {
    return <div className={[styles.shoutMenuWrapper, "unresponsive-text"].join(" ")}>
        <div className={styles.shoutMenu} >
            <h1>תצעקו איתנו</h1>
            <div className={styles.line}/>
            <ul>
                <li className={styles.Lech}>לך</li>
                <li className={styles.Semek}>ביבי הביתה</li>
            </ul>
            <span>המערכת יודעת לזהות את המילים האלה כרגע.. אנחנו עובדים על עוד אל תדאגו</span>
        </div>
        <h2 className={styles.instructions}>לחצו והחזיקו את מקש הרווח כדי שנקשיב ונשנה את הצעקה שלכם בהתאם</h2>

    </div>
}

export default Sidebar;