import styles from "./DisplayStyles.module.css"
import img1 from "./01.png"
import img4 from "./04.png"
import img5 from "./05.png"
import img6 from "./06.png"
import img7 from "./07.png"

const imgs = [img1, img4, img5, img6, img7]


const displayStyles = [
        {
        "name": "black",
        "style": styles.black
        },
        {
            "name": "pink",
            "style": styles.pink
        }];

const chooseRandomBackgroundImage = cached => {
    let img;
    let sessionImg = sessionStorage.getItem("backgroundImage");
    if (cached && sessionImg) {
        img = sessionImg
    } else {
        img = imgs[Math.floor(Math.random() * imgs.length)]
        if (!sessionImg) {
            sessionStorage.setItem("backgroundImage", img)
        }
    }
    return img;
}

export default displayStyles;

export {chooseRandomBackgroundImage}