import React, {useState} from "react"
import styles from "./ShoutDisplay.module.css";
import ml5 from "ml5";
import "../globals";
import displayStyles, {chooseRandomBackgroundImage} from "../display-styles/display-styles";
import "p5/lib/addons/p5.sound.min"
import p5 from "p5"
import P5Wrapper from "react-p5-wrapper"

const modelURL = 'https://teachablemachine.withgoogle.com/models/RsxEeTTdF/';
// const modelURL = "https://teachablemachine.withgoogle.com/models/DSz8jvO7_/";

const ShoutObject = ({displayStyle, responsive}) => {
    let [canvas, setCanvas] = useState();
    let Disp = "םתסאמנ םיתחשומ";
    let currentFont
    let label;
    let vol = 0;
    let Haim, Semek, Lech;
    let imgSrc;
    // const nonResponsiveSketch = p => {
    //     p.preload = () => {
    //         Lech = p.loadFont("static/media/aaLech.356b8241.otf");
    //         Haim = p.loadFont("static/media/Haim-Medium.e5694826.otf");
    //         Semek = p.loadFont("static/media/aasemek-Regular.33606ce0.otf");
    //     }
    //
    //     p.setup = () => {
    //         let width = p.canvas.parentElement.offsetWidth - 10, height = p.canvas.parentElement.offsetHeight - 10;
    //         currentFont = Haim;
    //         console.log(canvas);
    //         p.createCanvas(width, height);
    //         //canvas.parent(ref)
    //
    //     }
    //
    //     p.draw = () => {
    //         vol = (Math.random() * .2) + (vol * .8);
    //         Disp = label === "Lech" ? "ךל" : "התיבה יביב"
    //         currentFont = Disp === "Lech" ? Lech : Semek;
    //
    //         p.textFont(currentFont, Math.max(vol * p.height + 20, p.height));
    //         p.fill("#000000");
    //         p.textAlign(p.CENTER, p.CENTER);
    //         p.text(Disp, p.width / 2, p.height / 2);
    //     }
    // }
    const responsiveSketch = p => {
        let classifier;
        let mic;

        p.preload = () => {
            Lech = p.loadFont("static/media/aaLech.356b8241.otf");
            Haim = p.loadFont("static/media/Haim-Medium.e5694826.otf");
            Semek = p.loadFont("static/media/aasemek-Regular.33606ce0.otf");
            classifier = ml5.soundClassifier(modelURL + 'model.json', {probabilityThreshold: 0.7}, classifyAudio);
            currentFont = Haim;
            imgSrc = chooseRandomBackgroundImage(true);
            console.log(imgSrc);
            imgSrc = p.loadImage(imgSrc);
        }
        p.setup = () => {
            p.createCanvas(560, 450);
            // let css = window.getComputedStyle(p.canvas.parentElement);
            try {
                let width = p.canvas.parentElement.offsetWidth, height = p.canvas.parentElement.offsetHeight - 15;
                p.resizeCanvas(width, height);
            } catch (exception) {

            }
            //canvas.parent(ref);
            // Create the video
            while (mic === undefined) {
                mic = new p5.AudioIn();
            }
            mic.start();
        }

        p.windowResized = () => {
            let width = p.canvas.parentElement.offsetWidth - 10, height = p.canvas.parentElement.offsetHeight - 10;
            p.resizeCanvas(width, height);
        }

        function classifyAudio() {
            classifier.classify(gotResults);
        }

        p.draw = () => {
            if (p.canvas.width === 0) {
                p.windowResized()
            }
            p.background(255);
            try {
                p.image(imgSrc, 0, 0, p.canvas.width, p.canvas.height);
            } catch (exception) {}
            let vol;
            // STEP 4: Draw the label

            if (p.keyIsPressed === true) {
                if (label === "Lech") {
                    Disp = "ךל";
                    currentFont = Lech;
                } else if (label === "BB") {
                    Disp = "התיבה יביב";
                    currentFont = Semek;
                }
            }

            // Draw the text
            try {
                vol = mic.getLevel();
            } catch (exception) {
                mic = new p5.AudioIn();
                vol = mic.getLevel();
            }
            try {
                p.textFont(currentFont, Math.min(vol * p.height + 20, p.height / 4));
            } catch (exception) {}
            p.fill("#FF00DA");
            p.textAlign(p.CENTER, p.CENTER);
            p.text(Disp, p.width / 2, p.height / 2);
        }

        function gotResults(error, results) {
            // Something went wrong!
            if (error) {
                console.error(error);
                return;
            }
         //   console.log(results.map(s=> s.label + ": " + s.confidence));
            // Store the label and classify again!
            label = results[0].label;
        }
    }
    return <div className={[displayStyles.find(s => s.name === displayStyle).style, styles.defaultDisplayWrapper].join(" ")}>
        <P5Wrapper sketch={responsiveSketch} />
    </div>
}

const NamedShoutObject = ({displayStyle, name, city, responsive}) => {
    return <div className={styles.namedShoutObject}>
        <ShoutObject displayStyle={displayStyle} responsive={responsive} />
        <div>
            <span style={{display: "inline-block"}}>{name}</span>
            <span style={{display: "inline-block", marginRight: "0.7ch"}}>|</span>
            <span style={{display: "inline-block", marginRight: "0.7ch"}}>{city}</span>
        </div>
    </div>
}

export {ShoutObject, NamedShoutObject}

