import React from "react";
import styles from "./DisplayPicker.module.css"
import displayStyles, {chooseRandomBackgroundImage} from "../display-styles/display-styles";

const DisplayPicker = ({currentDisplayStyle, setDisplayStyle}) => {
    return<div className={styles.displayPickerWrapper}>
        <h2 className={styles.text}>להכניס לך קצת אוירה?</h2>
        <div className={styles.displayPicker} >
        {displayStyles.map(s => {
            let style = currentDisplayStyle === s.name ? [s.style, styles.displayOption, styles.selected].join(' ') : [s.style, styles.displayOption].join(" ");
            let backgroundImage = s.name === "pink" ? {backgroundImage: `url(${chooseRandomBackgroundImage(true)}`} : {};
            return <div className={style} style={backgroundImage} onClick={() => setDisplayStyle(s.name)}>
                <span />
            </div>
        })}
        </div>
    </div>
}

export default DisplayPicker;