
import styles from './App.module.css';
import React, { useState } from "react";
import Header from "./header/Header"
import Sidebar from "./sidebar/Sidebar";
import ShoutDisplay from "./shout-display/ShoutDisplay"

const App = () => {
  let currentDisplayStyle = "pink";
  let setDisplayStyle = () => {};
  let [currentLayout, setLayout] = useState("single")
  return (
    <div className={styles.App}>
      <Header changeLayout={setLayout} />
      <Sidebar setDisplayStyle={setDisplayStyle} currentDisplayStyle={currentDisplayStyle} />
      <ShoutDisplay currentDisplayStyle={currentDisplayStyle} currentLayout={currentLayout} />
    </div>
  );
}

export default App;
