import React, {useState, useEffect} from "react";
import {ShoutObject, NamedShoutObject} from "./ShoutObject"
import styles from "./ShoutDisplay.module.css"

const SingleDisplay = ({currentDisplayStyle}) => {
    return <ShoutObject displayStyle={currentDisplayStyle} responsive={true} />
}

const MultipleDisplay = ({currentDisplayStyle}) => {
    let [currentFriends, setCurrentFriends] = useState([]);
    useEffect(() => {
        updateCurrentFriends(setCurrentFriends);
        const interval = setInterval(() => {
            updateCurrentFriends(setCurrentFriends);
        }, 1500);
        return () => clearInterval(interval);
    }, []);

    return <div className={styles.multipleDisplay}>
        <NamedShoutObject name={localStorage.getItem("name")}
                          city={localStorage.getItem("city")}
                          displayStyle={currentDisplayStyle}
                          responsive={true}
        />
        {currentFriends.filter(s =>
            s.name !== localStorage.getItem("name")
            || s.city !== localStorage.getItem("city"))
            .map((s) => <NamedShoutObject {...s} responsive={false} />)}
    </div>
}

const updateCurrentFriends = async setCurrentFriends => {
    let promise = await fetch("http://localhost:5000/friends");
    let jsonData = await promise.json();
    setCurrentFriends(jsonData.users);
}

const ShoutDisplay = ({currentDisplayStyle, currentLayout}) => {

    return <div className={styles.shoutDisplay}>
        {
            currentLayout === "single"
            ? <SingleDisplay currentDisplayStyle={currentDisplayStyle} />
            : <MultipleDisplay currentDisplayStyle={currentDisplayStyle} />
        }
    </div>
}

export default ShoutDisplay;